export class PointInterest {
    
        idPointInterest: number;
        name: string;
        address: string;
        ray: number;
        zoomLevel: number;
        imageUri: string;
        coordinate: { lat: number, lng: number };
        type: 'MARKER' | 'POLYGON' | 'LINE' | number;
        decode: any[];
        hash: string;
    
        constructor() {
            this.ray = 100;
            this.decode = [];
            this.coordinate = { lat: 0, lng: 0 };
        }
    
    }
  
    
    
    
    
    
    
 
    
    
   

