import { MapService } from './../../utils/map.service';
import { DataManagementService } from './../../providers/data-management.service';
import { PointInterest } from './../../objects/data-management';
import { Stop } from './../../objects/real-time';
import { Component } from '@angular/core';
import { NavController, NavParams, ModalController,LoadingController,ViewController } from 'ionic-angular';
import {DatePipe} from '@angular/common';
import {Poi} from '../poi/poi'

 @Component({
  selector: 'page-stop-list',
  templateUrl: 'stop-list.html',
})
export class StopList {
    stops:any;
    StopArray = [];


  constructor(public navCtrl: NavController,private datamanagementService : DataManagementService,private mapService:MapService, public navParams: NavParams, public viewCtrl: ViewController,private modalCtrl:ModalController) {
    this.stops = navParams.get('stops');
    
  }
 
  drawStop(stop: any){
    this.viewCtrl.dismiss(stop);
  }
  closeModal(){
      
    this.viewCtrl.dismiss(null);
    
  }
  openPoiModal(stop:any){
    
    this.viewCtrl.dismiss(null);
    let pointInterest= new PointInterest();
    pointInterest.address=stop.geocodingDetails;
    pointInterest.coordinate.lat=stop.stopLatitude;
    pointInterest.coordinate.lng=stop.stopLongitude;
    let PoiModal = this.modalCtrl.create(Poi,{pointInterest});
    PoiModal.present();

    PoiModal.onDidDismiss(form => {
     this.datamanagementService.addPointInterest(form.pointinterest).subscribe(pointInterest => {

          if (pointInterest.type == "MARKER") {
              let circle = L.circle(pointInterest.coordinate, {
                  color: 'red',
                  fillColor: '#f03',
                  fillOpacity: 0.1,
                  radius: pointInterest.ray
              });
              this.mapService.addCircle(circle);
              this.mapService.map.setView({ lat: stop.stopLatitude, lng: stop.stopLongitude }, 15);
             

              setTimeout(()=> {
                  this.mapService.removeCirclesFromMap();
              }, 3000);
          }

          this.datamanagementService.pointInterests.push(pointInterest);
          this.drawStop(stop);
          
      }, (err) => {
          
      });
        
           
    });

  }
   

}


