import {Injectable} from '@angular/core';
import {Http} from "@angular/http";
import {Subscription} from "rxjs";
import {Observable} from "rxjs";
import { dns,nominatim_dns } from "../providers/global.config";
@Injectable()
export class GeocodingService {

    geocodingSubscription: Subscription;
    token: string;
    
    constructor(private _http: Http) {
    }

    distance(lat1, lon1, lat2, lon2) {
        var p = 0.017453292519943295;    // Math.PI / 180
        var c = Math.cos;
        var a = 0.5 - c((lat2 - lat1) * p) / 2 +
            c(lat1 * p) * c(lat2 * p) *
            (1 - c((lon2 - lon1) * p)) / 2;
        var distance = 12742 * Math.asin(Math.sqrt(a));
        return distance;
    }

    inverseGeoconding(latitude: number, longitude: number, zoom: number): Observable<any> {
        return this._http.get(nominatim_dns + '/reverse.php?format=json&lat=' + latitude + '&lon=' + longitude + '&zoom=' + zoom + '&accept-language=fr&addressdetails=1').map(res => res.json());
    }

    getMyIpAdress(): Observable<{ip: string}> {
        return this._http.get('http://api.ipify.org/?format=json').map(res => res.json());
    }

    getMyLocation(ip: string) {
        return this._http.get('freegeoip.net/json/' + ip).map(res => res.json());
    }

}


