import { Historical } from './../historical/historical';
import { Component } from '@angular/core';
import { NavController, ToastController, LoadingController, App } from 'ionic-angular';
import {SigningService} from '../../providers/login';
import {RealTimeService} from '../../providers/real-time.service';
import {HistoricalService} from '../../providers/historical.service';
import { DataManagementService } from '../../providers/data-management.service';
import { RealTimeRecord, Group, Vehicule } from "../../objects/real-time";
import { Subscription } from "rxjs";
import { Storage } from '@ionic/storage';
import {User} from '../../objects/real-time'


@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  buttonColor: string = '#c42e36';
  loader:any;
  showing: boolean;
  groups: Group[];
  login: String = null;
  mdp: String = null;
  currentuser:User;
  allGroups: Subscription;
  newRealTimeRecords: RealTimeRecord[] = null;
  oldRealTimeRecords: RealTimeRecord[] = null;
  allRealTimeRecords: Subscription;
  searchWord: string = '';
  loginTest : String = null;
  mdpTest : String = null;

  constructor(public storage: Storage,private historicalService: HistoricalService,public app : App,public loadingCtrl: LoadingController,private realTimeService: RealTimeService,private dataManagementService: DataManagementService, public navCtrl: NavController,private signinService: SigningService,private toastController: ToastController) {
    //recuperation current user
   this.currentuser= JSON.parse(localStorage.getItem('currentuser'));
  if(this.currentuser){
    this.login = this.currentuser.username;
    this.mdp=this.currentuser.password;
    this.goToRt();
  }else{
    this.login="";
    this.mdp="";
  }
  
  }   
 //Signing method
  Signing() {
    
    this.buttonColor = '#345465'; 
    this.signinService.login({"username":this.login,"password":this.mdp}).subscribe(
      currentUser => {
          currentUser.password = this.mdp;
          localStorage.setItem('currentuser',JSON.stringify(currentUser));
          this.goToRt();
        }
      , ()=> {
        let toast = this.toastController.create({
          message: 'Authentification Echoue!',
          duration: 2000
        });
        this.buttonColor = '#c42e36'; 
        toast.present();
      });
     
  }
  //GO to Historical
  goToRt(){
   this.navCtrl.push(Historical).then(() => {
      const index = this.navCtrl.getActive().index;
      this.navCtrl.remove(0, index);
    });
  }
  
}



