import { MapService } from './../../utils/map.service';
import { DataManagementService } from './../../providers/data-management.service';
import { Component } from '@angular/core';
import { NavController, NavParams, ModalController,LoadingController,ViewController } from 'ionic-angular';
import {DatePipe} from '@angular/common';
import {Mileage} from '../../objects/real-time'

 @Component({
  selector: 'page-mileage-list',
  templateUrl: 'mileage.html',
})
export class MileageComponent {
    mileages:Mileage;
    MileageArray = [];
    totalMileage: Mileage;
    
    constructor(public navCtrl: NavController,private datamanagementService : DataManagementService,private mapService:MapService, public navParams: NavParams, public viewCtrl: ViewController,private modalCtrl:ModalController) {
        this.mileages = navParams.get('mileages');
        this.totalMileage = navParams.get('totalMileage');
        
    
      }
      addMileageToArray(mileage: any, event) {
        if (!event.ctrlKey) {
            this.MileageArray = [];
            this.mapService.removePolylinesFromMap();
            this.mapService.removeMarkersFromMap();
        }

        this.MileageArray.push(mileage);
        this.viewCtrl.dismiss(mileage);
    }

    closeModal(){
        
      this.viewCtrl.dismiss(null);
      
    }
}



