import { PointInterest } from './../objects/data-management';
import { Storage } from '@ionic/storage';
import { CallNumber } from '@ionic-native/call-number';
import {Injectable} from '@angular/core';
import 'rxjs/add/operator/map';
import {Observable} from 'rxjs/Observable';
import {Http, Headers} from '@angular/http';
import {dns} from "./global.config";
import {RealTimeRecord, Group} from "../objects/real-time";
import {createAuthorizationHeader} from "../utils/headers";
import {User} from '../objects/real-time'

@Injectable()
export class RealTimeService {

    currentuser:User;
    
    constructor(private _http: Http,private storage:Storage) {
        this.currentuser= JSON.parse(localStorage.getItem('currentuser'));
        
    }

    getRealTimeRecord(deviceId: number): Observable<RealTimeRecord> {
        let headers = new Headers({'Accept': 'application/json'});
        headers.append('Authorization', this.currentuser.token);
        // ici il faut recuperer utilisateur courant !!!!
        // il faut dabord lors de authen => tu gard utilisteur sur le storage pour que tu puise le recuperer ici
        return this._http.get(dns + 'realTimeRecords/' + deviceId, {headers: headers}).map(res => res.json());

    }

    getAllPois(): Observable<PointInterest[]> {
        let headers = new Headers({'Accept': 'application/json'});
        headers.append('Authorization', this.currentuser.token);
        return this._http.get(dns + 'pointInterests/dtos', {headers: headers}).map(res => res.json());
    }

    getAllRealTimeRecords(): Observable<RealTimeRecord[]> {
        let headers = new Headers({'Accept': 'application/json'});
        let parms = "";
        headers.append('Authorization', this.currentuser.token);
       
        if (!this.currentuser.isRoot) {
            parms = '?id=' + this.currentuser.id;
        }
        return this._http.get(dns + 'realTimeRecords'+ parms, {headers: headers}).map(res => res.json());
    }

    getAllGroups(keyword: string): Observable<Group[]> {
        let headers = new Headers({'Accept': 'application/json'});
        headers.append('Authorization', this.currentuser.token);
        let parms = "";
        if (!this.currentuser.isRoot) {
            parms = '&id=' + this.currentuser.id;
        }
        return this._http.get(dns + 'groupes/details?keyword=' + keyword + parms, {headers: headers}).map(res => res.json());
      
        
       
    }

    getCurrentPath(deviceId: number): Observable<any> {
        let headers = new Headers({'Accept': 'application/json'});
        headers.append('Authorization', this.currentuser.token);
        return this._http.post(dns + 'paths/currentPath/' + deviceId,null,  {headers: headers}).map(res => res.json());
    }

    startEngine(idBoitier: number): Observable<Boolean> {
        let headers = new Headers({'Accept': 'application/json'});
        createAuthorizationHeader(headers)
        return this._http.post(dns + 'engine/start/' + idBoitier, null, {headers: headers}).map(res => res.json());
    }
    stopEngine(idBoitier: number): Observable<Boolean> {
        let headers = new Headers({'Accept': 'application/json'});
        createAuthorizationHeader(headers)
        return this._http.post(dns + 'engine/stop/' + idBoitier, null, {headers: headers}).map(res => res.json());
    }
    
}


