import { GeocodingService } from './../../utils/geocoding.service';
import { DataManagementService } from './../../providers/data-management.service';
import { MapService } from './../../utils/map.service';
import { PointInterest } from './../../objects/data-management';
import { Component,Input, Output } from '@angular/core';
import { NavController, NavParams, ModalController,AlertController,LoadingController,ViewController,ToastController } from 'ionic-angular';
import Polygon = L.Polygon;
import Marker = L.Marker;
import Icon = L.Icon;
import {imagesDir} from "../../providers/global.config";

 @Component({
    selector: 'app-poi',
    templateUrl: 'poi.html'
})
export class Poi{
    mode: string = 'ADD';
    
       
    
    pointInterests: PointInterest[] = [];
    
    pointInterestsPoint: PointInterest[] = [];
    pointInterestsPolygon: PointInterest[] = [];
    images = [
        {text: "marker1", uri: imagesDir + "marker1.png"},
        {text: "marker2", uri: imagesDir + "marker2.png"},
        {text: "marker3", uri: imagesDir + "marker3.png"},
        {text: "marker4", uri: imagesDir + "marker4.png"},
        {text: "marker5", uri: imagesDir + "marker5.png"},
        {text: "marker6", uri: imagesDir + "marker6.png"},
        {text: "marker7", uri: imagesDir + "marker7.png"},
        {text: "marker8", uri: imagesDir + "marker8.png"},
        {text: "marker9", uri: imagesDir + "marker9.png"},
        {text: "marker10", uri: imagesDir + "marker10.png"},
        {text: "marker11", uri: imagesDir + "marker11.png"},
        {text: "flag1", uri: imagesDir + "flag1.png"},
        {text: "flag2", uri: imagesDir + "flag2.png"},
        {text: "house1", uri: imagesDir + "house1.png"},
        {text: "house2", uri: imagesDir + "house.png"}
    ];
    pointinterest:PointInterest=new PointInterest();
 
    selectedImage: { text: string, uri: string } = this.images[0];
    addresspoi:string;
    latpoi:number;
    lngpoi:number;
    imageuripoi:any;
    constructor(public navParams: NavParams,private mapService: MapService,private alertCtrl: AlertController,public toastr: ToastController,private dataManagementService: DataManagementService, public viewCtrl: ViewController,private geocodingService: GeocodingService) {
        this.pointinterest = navParams.get('pointInterest');
    }
   
    

    //GET list of Points and Polygons
    
    closeModal(){
        this.pointinterest.name=null;
        this.viewCtrl.dismiss({pointinterest:this.pointinterest});
        
      }
 
    addpoi(){
        if(this.pointinterest.name==null){
            let alert = this.alertCtrl.create({
                title: 'Alert',
                subTitle: 'Ajouter un Nom',
                buttons: ['Ok']
              });
              alert.present();
        }else{
       
        this.pointinterest.type='MARKER';
        this.pointinterest.imageUri=this.selectedImage.uri;
        this.viewCtrl.dismiss({pointinterest:this.pointinterest });
        }
      }
      
           
                      
                    

}


