import { Credentials } from './../objects/real-time';
import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import 'rxjs/add/operator/map';
import { dns } from "./global.config";
import { contentHeaders, createAuthorizationHeader } from "../utils/headers";
import {User} from '../objects/real-time'
@Injectable()
export class SigningService {

  

  constructor(public http: Http) {
   
  }

  login(credentials: any) {
    return this.http.post(dns + 'signin', credentials, { headers: contentHeaders }).map(res => res.json());
  }

  logout() {
    let headers = new Headers({ 'Accept': 'application/json' });
    createAuthorizationHeader(headers);
    return this.http.post(dns + 'signout', {}, { headers: headers }).map(res => res.json());
  }

}



