import { Component } from '@angular/core';
import { NavController, NavParams, ModalController, ViewController,LoadingController } from 'ionic-angular';
import { HistoricalService } from '../../providers/historical.service';
import { Subscription } from "rxjs";


@Component({
  selector: 'page-historical-form',
  templateUrl: 'historical-form.html',
})
export class HistoricalForm {

  allGroups: Subscription;
  selectedDeviceId: any;
  groups: any[];
  selectedGroup: any;
  vehicules: any;
  startDate1: Date=new Date() ;
  endDate1: Date= new Date();
  startDate:any;
  selectedGroupId: any;
  endDate:any;
  loader: any;
  list:string="trajet";

  constructor(private historicalService: HistoricalService, public viewCtrl: ViewController, public navCtrl: NavController, public navParams: NavParams, public loadingCtrl: LoadingController) {
    
    this.startDate1.setTime(this.startDate1.getTime() - 3 * 86400000);
    this.startDate= this.startDate1.toISOString();
    this.endDate=this.endDate1.toISOString();
    
  }

  

 
  getAllPaths() {

    this.viewCtrl.dismiss({startDate: this.startDate, endDate: this.endDate ,list:this.list});
   
  }

  closeModal(){
   this.viewCtrl.dismiss({startDate:"",endDate:"",list:""});

  }

}



