import { Component } from '@angular/core';
import { NavController, NavParams, ToastController,ModalController,ViewController ,AlertController } from 'ionic-angular';
import { RealTimeService } from '../../providers/real-time.service';
import { Subscription } from "rxjs";


 @Component({
  selector: 'page-groups-page',
  templateUrl: 'groups-page.html',
})
export class GroupsPage {
  allGroups: Subscription;
  groups: any[];
  selectedDevice: number;
  searchWord : string;
  iconCall:boolean=false;

  constructor(private alertCtrl: AlertController,public navCtrl: NavController, public navParams: NavParams, public viewCtrl: ViewController, public realTimeService: RealTimeService) {
    this.groups = navParams.get('groups');
    //this.groups = this.processGroups(this.groups);
  }

 /* processGroups(groups: any){
    groups.forEach(group => {
      
    });
    return groups;
  }*/

  closeModal(){
    this.viewCtrl.dismiss(this.selectedDevice);
    
  }

  ionViewDidLoad() {
  }

  searchGroup(){
 this.allGroups = this.realTimeService.getAllGroups(this.searchWord).subscribe(groupes => {
      this.groups = groupes;
    });
  }

  goToRealTimeRecord(deviceId: number){
    this.selectedDevice = deviceId;
   
    this.viewCtrl.dismiss(this.selectedDevice);
   
  }

  AlertLancementMoteur(deviceId) {
    let alert = this.alertCtrl.create({
      title: 'Attention',
      message: 'tes-vous sr ?',
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          handler: () => {
            
          }
        },
        {
          text: 'Start',
          handler: () => {
          this.startEngine(deviceId);
          }
        }
      ]
    });
    alert.present();
  }
  startEngine(deviceId){
        this.realTimeService.startEngine(deviceId).subscribe(cmdSended => {
          alert("commande Lancement moteur envoy !");
      });
      
  
  }
  AlertArretMoteur(deviceId) {
    let alert = this.alertCtrl.create({
      title: 'Attention',
      message: 'tes-vous sr ?',
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          handler: () => {
            
          }
        },
        {
          text: 'Start',
          handler: () => {
          this.stopEngine(deviceId);
          }
        }
      ]
    });
    alert.present();
  }
  stopEngine(deviceId){
      this.realTimeService.stopEngine(deviceId).subscribe(cmdSended => {
          alert("commande Arrt moteur envoy !");
      });
  
  }

  displayTodaysMileage(){
    
  }

  displayCurrentPath(){
    
  }

}



